body {
  padding: 50px;
}

.icon {
  width: 40px;
  height: 20px;
  fill: wheat;
}

.switch {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  line-height: 0;
}

:root {
  --light: #d8dbe0;
  --dark: #28292c;
  --link: #1b8170;
  --link-hover: #185e52;
}

.toggle-switch {
  width: 40px;
  position: relative;
}

label {
  width: 100%;
  height: 20px;
  cursor: pointer;
  border-radius: 10px;
  position: absolute;
}

input {
  display: none;
  position: absolute;
}

@property --myColor1 {
  syntax: "<color>";
  inherits: false;
  initial-value: #4f2ee8;
}

@property --myColor2 {
  syntax: "<color>";
  inherits: false;
  initial-value: #dcdcdc;
}

.slider {
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, var(--myColor1) 0%, var(--myColor2) 100%);
  border-radius: 9px;
  transition: --myColor1 .5s, --myColor2 .5s;
  position: absolute;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

input:checked {
  transition: background 1s linear;
}

input:checked ~ .slider {
  --myColor1: #4f2ee8;
  --myColor2: #686868;
}

.slider:before {
  content: "";
  z-index: 4001;
  width: 18px;
  height: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

input:checked ~ .slider:before {
  background-color: var(--light);
  box-shadow: none;
  transform: translateX(20px);
}

.box {
  width: 100px;
  height: 100px;
  margin-top: 100px;
}

